import React from "react";
import { Row, Container,Col }       from "react-bootstrap";
import * as Icon                    from 'react-bootstrap-icons';
import '../css/footer.css'

function Footer(params) {
    const logo = {
        width:'20px',
        height:'20px'
    }
    return (
        <footer>
            <Container className="ps-5 pe-5">
                <Row>
                    <Col className="footer-col" lg={3} md={6} sm={12}>
                        <h2 className="footer-heading">About</h2>
                        <p>
                            Type the about us statement here.
                        </p>
                    </Col>
                    <Col className="footer-col" lg={3} md={6} sm={12}>
                        <h2 className="footer-heading">Links</h2>
                        <ul className="footer-link">
                            <li><a href="#home">Home</a></li>
                            <li><a href="#about">About</a></li>
                            <li><a href="#resume">Resume</a></li>
                            <li><a href="#contact">Contact</a></li>
                        </ul>
                    </Col>
                    <Col className="footer-col" lg={3} md={6} sm={12}>
                        <h2 className="footer-heading">Social</h2>
                        <ul className="footer-link">
                            <li>
                                <div className="footer-social">
                                    <a target="_blank" href="https://www.facebook.com/littleanuj/"><Icon.Facebook  height={logo.height} width={logo.width}/></a> 
                                </div>
                            </li>
                            <li>
                                <div className="footer-social">
                                    <a target="_blank" href="https://www.instagram.com/anuj.aes/"><Icon.Instagram height={logo.height} width={logo.width} /></a>
                                </div>
                            </li>
                            <li>
                                <div className="footer-social">
                                    <a target="_blank" href="https://www.linkedin.com/in/anuj-aes"><Icon.Linkedin  height={logo.height} width={logo.width}/></a>
                                </div>
                            </li>
                        </ul>
                    </Col>
                    <Col className="footer-col" lg={3} md={6} sm={12}>
                        <h2 className="footer-heading">Disclaimer</h2>
                        <p>
                            This Portfolio is designed using the ReactJs, React-Bootstrap and React-Bootstrap-Icons.
                            <br></br>
                            And this portfolio template is inspired from 'ColorLib'.
                        </p>
                    </Col>
                </Row>
            </Container>
        </footer>
    )
}

export default Footer;